<template>
    <div id="Software">
        <div id="box" class="container img-responsive" >
            <div id="right" >
                <h2 class="text-start" >{{ item.tittle }}</h2>
                <h4 class="text-break">{{ item.chtittle }}</h4>
                <img :src="item.logo" class="img-responsive">
                <blockquote class="color" >{{ Object.keys(item)[4] }}</blockquote>
                <p class="lead " v-html="item.简介"></p>
                 <blockquote  class="color">全文速览</blockquote>
                <p class="lead" v-html="item.全文速览"></p>
                <blockquote  class="color" >研究方法</blockquote>
                <p class="lead" id="search" v-html="item.研究方法"></p>
                <blockquote  class="color">结果与讨论</blockquote>
                <p class="lead" v-html="item.结果与讨论"></p>
                <blockquote  class="color">研究结论</blockquote>
                <p class="lead img-responsive" v-html="item.研究结论"></p>
                <blockquote class="color">作者介绍</blockquote>
                <p class="lead">{{ item.作者介绍 }}</p>
            </div>
        </div>
    </div>
  </template>
  <script>
  import { WOW } from "wowjs";
  export default {
    name: 'containbox_detail',
    props: {
      
    },
    data(){
        return{
            item:{},
            
        }
    },
    mounted() {
        var wow = new WOW();
        wow.init();
        this.item = this.$route.query
    },
  }
  </script>
  <style scoped>
  >>> #box{
    display: grid;
    justify-content:center;
    align-content:center;
  
  }
 >>> #right{
    padding: 50px 0;
    display: grid;
    grid-template-columns:1fr; 
    justify-content:center;
    align-content:center;
  }
  >>> #right h2{
    font-weight: 900;
  }
  >>> .fontsize{
    font-size:clamp(2rem,7vw,4rem);
  }
  .color{
    border-left: 5px solid #fce27c;
  }
  >>> #left{
    margin: 50px 0;
  }
  .left-container{
    width: 10vw;
    margin: 0 auto;
    border: 1px solid #474747;
    border-radius: 5px;
  }
  .left-container>p{
    text-align: center;
    line-height: 4;
    padding: 0;
    margin: 0;
    background: #474747;
    color: #fff;
    font-size: 1vw;
    font-weight: bold;
  }
  .left-container>li {
  text-align: center;
  height: 6vh;
  line-height: 3;
  margin: 0;
  border-top: 1px solid #474747;
  font-size: 1vw;
  }
  .left-container>li>a{
    text-decoration: none;
  }
  .left-container>li:hover{
    background: #928989;
  } 
 >>> img {
    margin: 0 auto;
    max-width: 100%;
    height: auto;
    display: block;
  }
>>> small{
  text-align: center;
   margin: 0 auto;
   display: block;
}
  @media screen and (max-width: 768px){
    #right{
        padding: 15px;
    }
  }
  </style>
  


